
.form {
	position: relative;
	width: 100vw;
	height: 100vh;
	overflow: hidden;
	&__map {
		position: absolute;
		top: 0;
		left: 0;
		width: 100vw;
		height: 100vh; } }

.inputs {
	position: absolute;
	bottom: 0;
	left: 0;
	z-index: 50;
	width: 100%;
	margin: 0 auto;
	padding: 20px;
	background-color: rgba(white, .9);
	&__container {
		display: flex;
		flex-direction: column;
		align-items: center; } }

.address {
	position: relative;
	display: flex;
	flex-direction: column;
	margin: 10px 0;
	background-color: white;
	width: 100%;
	&__address {
		width: 100%;
		display: flex;
		align-items: center; }
	&__point {
		margin: 0;
		width: 40px;
		text-align: center;
		font-family: 'Roboto Mono', monospace;
		font-size: 30px;
		font-weight: 700;
		color: orange; }
	&__input {
		box-sizing: border-box;
		width: calc(78% - 20px);
		height: 30px;
		padding: 5px;
		border: 1px solid rgba(gray, .5);
		&:hover {
			border: 1px solid rgba(gray, .5);
			outline: 0;
			outline-offset: 0; }
		&:active, &:focus {
			border: 2px solid rgba(255, 214, 11, 1);
			outline: 0;
			outline-offset: 0; } }
	&__input-other {
		width: 22%;
		border-left: none; }
	&__icon {
		margin-right: 20px;
		text-align: center;
		font-size: 25px;
		line-height: 30px;
		color: gray;
		&:hover {
			color: black; } } }

.hints {
	display: none;
	z-index: 100;
	position: absolute;
	top: 100%;
	right: 0px;
	width: calc(100% - 35px);
	margin: 0;
	padding: 0;
	background-color: white;
	border-left: 1px solid rgba(gray, .5);
	border-right: 1px solid rgba(gray, .5);
	border-bottom: 1px solid rgba(gray, .5);
	&__hint {
		padding: 0;
		list-style-type: none;
		margin: 0;
		padding: 2px 10px;
		cursor: pointer;
		&:hover {
			background-color: rgba(255, 214, 11, .5); } }
	&__hint-desc {
		font-size: 12px;
		color: rgba(gray, .7); } }

.submit {
	border: none;
	background-color: rgba(255, 214, 11, 1);
	width: 150px;
	height: 50px;
	font-size: 18px; }

.trip {
	display: flex;
	align-items: center;
	height: 50px;
	&__data {
		padding: 10px; } }

